import { Component, OnInit } from '@angular/core';
import { ApiService } from 'app/services/api.service';
import { Question } from 'app/models/question.model';
import { StudentPoint } from 'app/models/student-point.model';
import { Student } from 'app/models/student.model';
import { Router } from '@angular/router';
import { saveAs } from 'file-saver';
import * as XLSX from 'xlsx';
// declare const $: any;

@Component({
  selector: 'app-course-score',
  templateUrl: './course-score.component.html',
  styleUrls: ['./course-score.component.css']
})
export class CourseScoreComponent implements OnInit {
  
  public course_name: string;
  public course_code: string;
  public course_year: string;
  i: number;
  questions: Array<Question>;
  points: Array<StudentPoint>;
  students: Array<Student>;
  student: Array<StudentPoint> = [];
  studentsTop:Array<StudentPoint> = [];
  studentsScore:Array<StudentPoint> = [];
  student1: StudentPoint;
  student2: StudentPoint;
  student3: StudentPoint;
  public statusScore: string;
  constructor(
    private apiService: ApiService,
    private router: Router
    ) { }
  

  ngOnInit(): void {
    this.fatchCourse();
    this.fatchQuestionCourse();
    this.fatchScore();
    this.fatchStudentsCourse();
    this.fatchStudentRank();
    this.statusScore = 'scoreAll';
  }

  fatchCourse(){
    this.apiService.courseSelect().subscribe( () => {
      this.course_name = this.apiService.courseSelects.course_name;
      this.course_code = this.apiService.courseSelects.course_code;
      this.course_year = this.apiService.courseSelects.course_year;
    });
  }

  fatchStatus(status: string){
    this.statusScore = status;
  }

  fatchStudentRank(){
    this.apiService.studentRank().subscribe( (res) => {
      this.student = this.apiService.studentRanks;
      this.studentsTop = this.student.sort((a,b) => b.submitsession_point_total - a.submitsession_point_total);
      this.student1 = this.studentsTop[0];
      this.student2 = this.studentsTop[1];
      this.student3 = this.studentsTop[2];
      for(this.i=3;this.i<this.studentsTop.length;this.i++){
        this.studentsScore.push(this.studentsTop[this.i]);
      }
    });
  }

  fatchQuestionCourse(){
    this.apiService.getQuestionCourse().subscribe( () => {
      this.questions = this.apiService.questionsCourse;
    });
  }

  fatchScore(){
    this.apiService.studentPointAll().subscribe( () => {
      this.points = this.apiService.studentsPoint;
    });
  }

  fatchStudentsCourse(){
    this.apiService.studentCourse().subscribe( () => {
      this.students = this.apiService.studentsCourse;
    });
  }

  goToDetailScore(answerId: string, studentId:string){
    localStorage.setItem('answerId', answerId);
    localStorage.setItem('studentId', studentId);
    this.apiService.studentPointSession().subscribe( () => {
      this.router.navigate(['/course/course-student-score']);
    });
  }

  exportexcel(): void {
    const fileName= this.course_code+'_'+this.course_year+'.xlsx';
    let element = document.getElementById('excel-table'); 
    const ws: XLSX.WorkSheet =XLSX.utils.table_to_sheet(element);
    const wb: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');
    XLSX.writeFile(wb, fileName);
  }

}
