import { Component, OnInit } from '@angular/core';
import { Question } from 'app/models/question.model';
import { QuestionService } from 'app/services/question.service';
import { Tag } from 'app/models/tag.model';
import { Router } from '@angular/router';
import { ApiService } from 'app/services/api.service';
import swal from 'sweetalert2';
import { QuestionTags } from 'app/models/question-tag.model';

@Component({
  selector: 'app-teacher-assingment',
  templateUrl: './teacher-assingment.component.html',
  styleUrls: ['./teacher-assingment.component.css']
})
export class TeacherAssingmentComponent implements OnInit {

  questions: Array<QuestionTags> = [];
  questionsTag: Array<QuestionTags> = [];
  tags: Array<Tag> = [];
  filtering: any;
  check: any;
  id = [];
  i: number;
  j: number;
  // public filtering = 'normal';
  delete = [];
  public strinfTag: string;
  questionId: string;
  constructor(
    private apiService: ApiService,
    private router: Router,
    private questionService: QuestionService,
  ) { }

  ngOnInit(): void {
    this.fatchQuestion();
    this.fatchTags();
    this.filtering = 'normal';
  }


  fatchQuestion(){
    this.apiService.getQuestion().subscribe( (questionsArr) => {
      this.questions = (this.apiService.question).reverse();      
      this.id = this.questions;
    });
  }

  fatchTags(){
    this.apiService.allTags().subscribe( () => {
      this.tags = this.apiService.tags_tags;
    });
  }

  filter(event){
    this.filtering = event.target.value;
    if(this.filtering == 'normal'){
      this.fatchQuestion();
    }else if(this.filtering == 'max'){
      this.questions = (this.apiService.question).sort((a,b) => b.question_point - a.question_point);
    }else if(this.filtering == 'min'){
      this.questions = (this.apiService.question).sort((a,b) =>a.question_point - b.question_point);
    }else{
      this.questionsTag = [];
      (this.apiService.question).filter((q) => {
        q.question_tags.filter((tag, index) => {
          if(tag.tag_tag === this.filtering){
            this.questionsTag.push(q);
          }
        })}
      );
      this.questions = this.questionsTag;
    }

  }

  deleteQuestion(){
    swal.fire({
      title: '',
      text: '?' ,
      showCancelButton: true,
      confirmButtonColor: '#dc3545',
      cancelButtonColor: 'btn btn-danger',
      confirmButtonText: '',
      reverseButtons: true,
    }).then((result) => {
      if (result.value) {
        for(this.i=0;this.i<this.delete.length;this.i++){
          this.questionService.deleteQuestion(this.delete[this.i]).subscribe( () => {
            this.fatchQuestion();
          });
        }
        // this.questionService.deleteQuestion(this.questionId).subscribe( () => {
        //   this.fatchQuestion();
        // });
        swal.fire(
          '!',
          '',
          'success'
        )
        this.fatchQuestion();
      } else if (
        result.dismiss === swal.DismissReason.cancel
      ) {
        swal.fire(
          '',
          '',
          'error'
        )}
    });
    // this.questionService.deleteQuestion(this.questionId).subscribe( () => {
    //   this.fatchQuestion();
    // });
  }

  selectQuestion(selectId: string){
    localStorage.setItem('questionId', selectId);
    this.apiService.selectQuestion().subscribe( () => {
      this.router.navigate(['/teacher/teacher-edit-question']);
    });
  }

  idDelete(id: string){
    this.questionId = id;
    this.delete.push(this.questionId);
    
  }

}
