import { Component, OnInit } from '@angular/core';
import { ApiService } from 'app/services/api.service';
import { Student } from 'app/models/student.model';

@Component({
  selector: 'app-student-profile',
  templateUrl: './student-profile.component.html',
  styleUrls: ['./student-profile.component.css']
})
export class StudentProfileComponent implements OnInit {

  public toEditStudent: Student;
  fileToUpload: File;
  public newpassword;
  public oldpassword;
  constructor(
    private apiService: ApiService,
  ) { }

  ngOnInit(): void {
    this.fatchProfile();
  }

  fatchProfile(){
    // editProfileTeacherSelect
		this.apiService.editProfileStudentSelect().subscribe((response: any) => {
      this.toEditStudent = JSON.parse(JSON.stringify(this.apiService.toEditStudent));
    });

  }

  sandName(newName: string){
    this.apiService.editProfileName(newName).subscribe((response: any) => {
      this.fatchProfile();
    });
  }

  sandEmail(newEmail: string){
    this.apiService.editProfileEmail(newEmail).subscribe((response: any) => {
      this.fatchProfile();
    });
  }

  settingPassword(oldpassword: string, newpassword: string){
    this.apiService.editProfilePassword(oldpassword, newpassword).subscribe((response: any) => {
      this.fatchProfile();
    });
  }

  onFileChanged(event) {
    const fileList: FileList = event.target.files;
    if (fileList.length > 0) {
      this.fileToUpload = fileList[0];
    }
  }

  import() {
    if (!this.fileToUpload) {
      return;
    }
    this.apiService.editProfilePicture(this.fileToUpload).subscribe();
  }

}
