import { Component, OnInit } from '@angular/core';
import { StudentService } from 'app/services/student.service';
import { Student } from 'app/models/student.model';
import { ApiService } from 'app/services/api.service';
import { Course } from 'app/models/course.model';
import { StudentPoint } from 'app/models/student-point.model';

@Component({
  selector: 'app-student-score',
  templateUrl: './student-score.component.html',
  styleUrls: ['./student-score.component.css']
})
export class StudentScoreComponent implements OnInit {

  students: Array<StudentPoint> = [];
  studentsTop:Array<StudentPoint> = [];
  studentsScore:Array<StudentPoint> = [];
  public student1: StudentPoint;
  public student2: StudentPoint;
  public student3: StudentPoint;
  i: number;
  courseName: Course;
  public course_name: string;
  public course_code: string;
  public course_year: string;

  constructor(
    private apiService: ApiService
  ) { }

  ngOnInit(): void {
    this.fatchStudentRank();
    this.fatchCourseName();
  }

  fatchStudentRank(){
    this.apiService.studentRank().subscribe( (res) => {
      this.students = this.apiService.studentRanks;
      this.studentsTop = this.students.sort((a,b) => b.submitsession_point_total - a.submitsession_point_total);
      this.student1 = this.studentsTop[0];
      this.student2 = this.studentsTop[1];
      this.student3 = this.studentsTop[2];
      for(this.i=3;this.i<this.studentsTop.length;this.i++){
        this.studentsScore.push(this.studentsTop[this.i]);
      }
    });
  }
  

  fatchCourseName(){
    this.apiService.courseSelect().subscribe( () => {
      this.course_name = this.apiService.courseSelects.course_name;
      this.course_code = this.apiService.courseSelects.course_code;
      this.course_year = this.apiService.courseSelects.course_year;
    });
  }

}
