import { Component, HostBinding, NgModule } from '@angular/core';
import { UpgradableComponent } from 'theme/components/upgradable';
import { CourseService } from 'app/services/course.service';
import { Course } from 'app/models/course.model';
import { ApiService } from 'app/services/api.service';

@Component({
  selector: 'app-employer-form',
  styleUrls: ['./employer-form.component.scss'],
  templateUrl: 'employer-form.component.html',
})
export class EmployerFormComponent extends UpgradableComponent{

  public course_id: string;
  public course_name: string;
  public course_year: string
  public course: Course = new Course(this.course_id, this.course_name, this.course_year);


  constructor(private apiService: ApiService){
    super();
  }

  ngOnInit(): void {
    //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
    //Add 'implements OnInit' to the class.
    
  }

  addCourse(code: string, name: string, year: string){
    if(!code){
      return
    }
    if(!name){
      return
    }
    if(!year){
      return
    }
    this.apiService.addCourse(code, name, year).subscribe( (res) => {
    });
  }

}
