import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { MDBBootstrapModule } from 'angular-bootstrap-md';

import { AuthInterceptor } from '../app/services/auth/auth.interceptor';
import { AuthService } from '../app/services/auth/auth.service';
import { FakeBackendInterceptor } from '../app/services/auth/fakebackend.interceptor';

// import { MyComponentModule } from '';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { ChartsModule } from './pages/charts';
import { ComponentsModule } from './pages/components';
import { DashboardModule } from './pages/dashboard';
import { Dashboard2Module } from './pages/dashboard2';
import { FormsModule } from './pages/forms';
import { StudentExcersieModule } from './pages/student-excersie';
import { StudentScoreModule } from './pages/student-score';
import { StudentProfileModule } from './pages/student-profile';
import { TeacherHomeModule } from './pages/teacher-home';
import { TeacherCourseModule } from './pages/teacher-course';
import { TeacherAssingmentModule } from './pages/teacher-assingment';
import { TeacherProfileModule } from './pages/teacher-profile';
import { CourseAddStudentModule } from './pages/course-add-student';
import { CourseListStudentModule } from './pages/course-list-student';
import { CourseAssingmentModule } from './pages/course-assingment';
import { CourseScoreModule } from './pages/course-score';
import { HighlightService } from './services/highlighting.service';

@NgModule({
  declarations: [AppComponent],
  imports: [
    // MyComponentModule,
    BrowserModule,
    AppRoutingModule,
    ComponentsModule,
    DashboardModule,
    Dashboard2Module,
    FormsModule,
    ChartsModule,
    HttpClientModule,
    MDBBootstrapModule.forRoot(),
    StudentExcersieModule,
    StudentScoreModule, 
    StudentProfileModule, 
    TeacherHomeModule,
    TeacherCourseModule, 
    TeacherAssingmentModule, 
    TeacherProfileModule, 
    CourseAddStudentModule, 
    CourseListStudentModule, 
    CourseAssingmentModule, 
    CourseScoreModule,

  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ],
  providers: [
    AuthService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true,
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: FakeBackendInterceptor,
      multi: true,
    },
    HighlightService
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
