@NgModule({
  imports: [
    LayoutsModule,
    RouterModule.forRoot(
      [
        { path: '', redirectTo: '/pages/login', pathMatch: 'full' },
        { path: 'teacher', component: CommonLayoutComponent, children: [
          { path: 'dashboard', component: DashboardComponent, pathMatch: 'full' },
          { path: 'assignment', component: Dashboard2Component, pathMatch: 'full' },
          { path: 'logout', component: MapComponent, pathMatch: 'full' },
          { path: 'course', component: FormsComponent, pathMatch: 'full' , children:[
            // { path: 'add', component: }
          ]},
          { path: 'questioncourse', component: ChartsComponent, pathMatch: 'full' },
          { path: 'listcourse', component: ComponentsComponent, pathMatch: 'full' },
          { path: 'question', component: AssignmentComponent, pathMatch: 'full' },

          { path: 'teacher-home', component: TeacherHomeComponent, pathMatch: 'full' },
          { path: 'teacher-course', component: TeacherCourseComponent, pathMatch: 'full' },
          { path: 'teacher-assingment', component: TeacherAssingmentComponent, pathMatch: 'full' },
          { path: 'teacher-profile', component: TeacherProfileComponent, pathMatch: 'full' },
          { path: 'teacher-edit-question', component: TeacherEditQuestionComponent, pathMatch: 'full' },


          { path: 'student-home', component: StudentHomeComponent, pathMatch: 'full' },
          { path: 'student-assingment', component: StudentExcersieComponent, pathMatch: 'full' },
          { path: 'student-profile', component: StudentProfileComponent, pathMatch: 'full' },
          { path: 'student-score', component: StudentScoreComponent, pathMatch: 'full' },
          { path: 'student-work', component: StudentWorkAssingmentComponent, pathMatch: 'full' },

        ]}, 
        { path: 'course', component: CommonCourseComponent, children: [
          { path: 'course-student', component: CourseAddStudentComponent, pathMatch: 'full' },
          { path: 'course-assingment', component: CourseAssingmentComponent, pathMatch: 'full' },
          { path: 'course-list', component: CourseListStudentComponent, pathMatch: 'full' },
          { path: 'course-score', component: CourseScoreComponent, pathMatch: 'full' },
          { path: 'course-student-score', component: CourseStudentScoreComponent, pathMatch: 'full' },
          { path: 'questioncourse', component: ChartsComponent, pathMatch: 'full' },
        ]},
        { path: 'student', component: CommonStudentComponent, children: [
          { path: 'student-home', component: StudentHomeComponent, pathMatch: 'full' },
          { path: 'student-assingment', component: StudentExcersieComponent, pathMatch: 'full' },
          { path: 'student-profile', component: StudentProfileComponent, pathMatch: 'full' },
          { path: 'student-score', component: StudentScoreComponent, pathMatch: 'full' },
          { path: 'student-work', component: StudentWorkAssingmentComponent, pathMatch: 'full' },
        ]},

        { path: 'ui', loadChildren: './pages/ui/ui.module#UIModule' },
        { path: 'maps', loadChildren: './pages/maps/maps.module#MapsModule' },
        { path: 'pages', loadChildren: './pages/pages/pages.module#PagesModule' },
        { path: '**', redirectTo: '/pages/404' },
        { path: 'login', redirectTo: '/pages/login' },
      ],
      { useHash: true },
    ),
    
  ],
  exports: [RouterModule],
})

export class AppRoutingModule {}
