import { Component, OnInit, Inject, AfterViewInit } from '@angular/core';
import { UpgradableComponent } from 'theme/components/upgradable';
import { QuestionService } from 'app/services/question.service';
import { Question } from 'app/models/question.model';
import { Tag } from 'app/models/tag.model';
import { Testcase } from 'app/models/testcase.model';
import { HighlightService } from 'app/services/highlighting.service';
import {COMMA, ENTER} from '@angular/cdk/keycodes';
import {MatChipInputEvent} from '@angular/material/chips';
import { ApiService } from 'app/services/api.service';
import { SyntaxHighlightingService } from './syntax-highlighting.service';
export interface Tags {}
export interface Tests {
  testcase_testcase: string;
}

// export interface Test {
//   testcase_testcase: string;
// }

@Component({
  selector: 'app-dashboard-2',
  styleUrls: ['./dashboard2.component.scss'],
  templateUrl: './dashboard2.component.html',
})
export class Dashboard2Component extends UpgradableComponent implements AfterViewInit{

  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  tags: Tags[] = ['If-else'];
  questions: Array<Question> = [];
  public tag;
  testcases: Testcase[];
  name: string;
  proposition: string;
  point: string;
  guide: string;
  tag_tag: string;
  tastcase: string;
  error_complie: string;
  id: string;
  tests: string;
  example: string;
  question: Question = new Question(this.id, this.name, this.proposition, 
    this.point, this.guide, this.tests, this.example);
  // tag: Tag = new Tag(this.tag_tag);
  test: Testcase = new Testcase(this.tastcase);
  blogPost: './dashboard2.component.html';
  highlighted: boolean = false;
  testGuide: string = 'public class Test {';
  testGuide2: string = '}';
  quetionGuide: string = 'public class {}';
  testImport;
  testCase: Tests[] = [{testcase_testcase: ''}];
  constructor(
    private apiService: ApiService,
    // @Inject(HighlightService) private highlightService: HighlightService,
    @Inject(SyntaxHighlightingService) private readonly _syntaxHighlighter: SyntaxHighlightingService
  ){
    super();
  }

  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    this.tags.push(value);
    if (input) {
      input.value = '';
    }
  }

  remove(tags: Tags): void {
    const index = this.tags.indexOf(tags);

    if (index >= 0) {
      this.tags.splice(index, 1);
    }
  }


  ngOnInit(){
    //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
    //Add 'implements OnInit' to the class.
    this.ngAfterViewInit();
    // this.testCase = [];
  }

  public ngAfterViewInit(): void {
    this._syntaxHighlighter.highlightAll();
  }

  addQuestion(question: Question, quetionGuide: string, a:string, b:string, c:string, d:string){
    const name = question.question_name;
    const properties = question.question_proposition;
    const points = question.question_point;
    const guide = quetionGuide;
    const example = question.question_example;
    // {tag_tag: value.trim()}
    this.testCase.push({testcase_testcase: a.trim()});
    this.testCase.push({testcase_testcase: b.trim()});
    this.testCase.push({testcase_testcase: c.trim()});
    this.testCase.push({testcase_testcase: d.trim()});
    const toTestcase = (JSON.stringify(this.testCase));
    this.apiService.addQuestion(name, properties, points, guide, toTestcase , this.tags, example).subscribe();
    
  }

  onEvent(e: any){
    const latestValue = e.target.value;
    this.testGuide = 'public class '+latestValue+'Test {';
    this.quetionGuide = 'public class '+latestValue+' {}';
    this.testCase = [];
  }

  compileQuestion(question: Question, quetionGuide: string, a:string, b:string, c:string, d:string){
    const name = question.question_name;
    this.testCase.push({testcase_testcase: a.trim()});
    this.testCase.push({testcase_testcase: b.trim()});
    this.testCase.push({testcase_testcase: c.trim()});
    this.testCase.push({testcase_testcase: d.trim()});
    const toTestcase = (JSON.stringify(this.testCase));
    this.apiService.questionComplie(name, quetionGuide, toTestcase).subscribe(res => {
      this.error_complie = this.apiService.error_complies;
      this.testCase.splice(0, this.testCase.length);
    });
  }


}
