import { Component, OnInit } from '@angular/core';
import { ApiService } from 'app/services/api.service';
import { SubmitSession } from 'app/models/submitsession.model';
import { Router } from '@angular/router';
import { Student } from 'app/models/student.model';

@Component({
  selector: 'app-course-student-score',
  templateUrl: './course-student-score.component.html',
  styleUrls: ['./course-student-score.component.css']
})
export class CourseStudentScoreComponent implements OnInit {

  studentScores: Array<SubmitSession> ;
  student: Student;
  length: number;
  id: string;
  constructor(
    private apiService: ApiService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.fatchStudentScore();
  }

  fatchStudentScore(){
    this.apiService.studentPointSession().subscribe( () => {
      this.studentScores = this.apiService.submitStudent;
      this.length = this.studentScores.length;
    });
    this.apiService.editProfileStudent().subscribe( () => {
      this.student = this.apiService.toEditStudents;
    });
    this.id = localStorage.getItem('studentId');
  }

  goBack(){
    localStorage.setItem('answerId', '');
    localStorage.setItem('studentId', '');

    this.router.navigate(['/course/course-score']);

  }

}
