import { Component, OnInit } from '@angular/core';
import { ApiService } from 'app/services/api.service';
import { Teacher } from 'app/models/teacher.model';

@Component({
  selector: 'app-teacher-profile',
  templateUrl: './teacher-profile.component.html',
  styleUrls: ['./teacher-profile.component.css']
})
export class TeacherProfileComponent implements OnInit {

  public toEditTeachers: Teacher;
  public picture: string;
  public username: string;
  public fileToUpload: File;
  public oldpassword;
  public newpassword;
  constructor(
    private apiService: ApiService,
  ) { }

  ngOnInit(): void {
    this.fatchProfile();
  }

  fatchProfile(){
    // editProfileTeacherSelect
		this.apiService.editProfileTeacherSelect().subscribe((response: any) => {
      this.toEditTeachers = JSON.parse(JSON.stringify(this.apiService.toEditTeacher));
      this.picture = 'http://localhost/javacodewars/imagesProfile/'+this.toEditTeachers.teacher_picture;
      this.username = this.apiService.usernane;
    });
    
  }

  sandEmail(newEmail: string){
    this.apiService.editProfileEmail(newEmail).subscribe((response: any) => {
      this.fatchProfile();
    });
  }

  settingPassword(oldpassword: string, newpassword: string){
    this.apiService.editProfilePassword(oldpassword, newpassword).subscribe((response: any) => {
      this.fatchProfile(); 
    });
    
  }

  onFileChanged(event) {
    const fileList: FileList = event.target.files;
    if (fileList.length > 0) {
      this.fileToUpload = fileList[0];
    }
  }

  import() {
    if (!this.fileToUpload) {
      return;
    }
    this.apiService.editProfilePicture(this.fileToUpload).subscribe();
  }


}
