import { Component, OnInit } from '@angular/core';
import { CourseService } from 'app/services/course.service';
import { Course } from 'app/models/course.model';
import { AuthService } from 'app/services';
import { MenuItemModel } from '@syncfusion/ej2-angular-navigations'; 
import { enableRipple } from '@syncfusion/ej2-base';
import { Router } from '@angular/router';
import { ApiService } from 'app/services/api.service';
import { CommonLayoutComponent } from 'app/layouts/common-layout';
import swal from 'sweetalert2';
import { Question } from 'app/models/question.model';

enableRipple(true);
@Component({
  selector: 'app-teacher-course',
  templateUrl: './teacher-course.component.html',
  styleUrls: ['./teacher-course.component.css']
})
export class TeacherCourseComponent implements OnInit {

  courses: Array<Course> = [];
  public statusCourses: string;
  questions: Array<Question>;

  constructor(
    private courseService: CourseService,
    private authService: AuthService,
    private router: Router,
    private apiService: ApiService,
    private sideBar: CommonLayoutComponent
  ) { }

  ngOnInit(): void {
    this.fatchAllCourse();
    localStorage.setItem('courseId', '');
  }
  
  fatchAllCourse(){
    this.apiService.allCourse().subscribe( (res) => {
      this.courses = this.apiService.courses ;
    });
  }

  statusCourse(courseId: string, status: string){
    if(status == 'Delete'){
      swal.fire({
        title: '',
        text: '?' ,
        showCancelButton: true,
        confirmButtonColor: '#dc3545',
        cancelButtonColor: 'btn btn-danger',
        confirmButtonText: '',
        reverseButtons: true,
      }).then((result) => {
        if (result.value) {
          this.apiService.courseHide(courseId, status).subscribe( (res) => {
            this.fatchAllCourse();
          });
          swal.fire(
            '!',
            '',
            'success'
          )
          this.fatchAllCourse();
        } else if (
          result.dismiss === swal.DismissReason.cancel
        ) {
          swal.fire(
            '',
            '',
            'error'
          )}
      });
    }

    if(status == 'Hide'){
      swal.fire({
        title: '',
        text: '?' ,
        showCancelButton: true,
        confirmButtonColor: '#dc3545',
        cancelButtonColor: 'btn btn-danger',
        confirmButtonText: '',
        reverseButtons: true,
      }).then((result) => {
        if (result.value) {
          this.apiService.courseHide(courseId, status).subscribe( (res) => {
            this.fatchAllCourse();
          });
          
          swal.fire(
            '!',
            '',
            'success'
          )
          this.fatchAllCourse();
        } else if (
          result.dismiss === swal.DismissReason.cancel
        ) {
          swal.fire(
            '',
            '',
            'error'
          )}
      });
    }

    this.fatchAllCourse();

  }

  refresh(): void {
    window.location.reload();
  }

  goToCourse(courseId: string){
    localStorage.setItem('courseId', courseId);
    this.router.navigate(['/course/course-student']);
    
  }


}
