import { Component, OnInit } from '@angular/core';
import { ApiService } from 'app/services/api.service';
import { Question } from 'app/models/question.model';
import { Tag } from 'app/models/tag.model';
import { QuestionTags } from 'app/models/question-tag.model';

@Component({
  selector: 'app-course-assingment',
  templateUrl: './course-assingment.component.html',
  styleUrls: ['./course-assingment.component.css']
})
export class CourseAssingmentComponent implements OnInit {

  public course_name: string;
  public course_code: string;
  public course_year: string;
  questions: Array<QuestionTags>;
  questionsTag: Array<QuestionTags> = [];
  questionsSelect: Array<QuestionTags>;
  questionId: string;
  delete = [];
  i: number;
  filtering: any;
  tags: Array<Tag> = [];


  constructor(private apiService: ApiService) { }

  ngOnInit(): void {
    this.fatchCourse();
    this.filtering = 'normal';
    this.fatchQuestionCourseSelect();
    this.fatchTags();

  }

  fatchTags(){
    this.apiService.allTags().subscribe( () => {
      this.tags = this.apiService.tags_tags;
    });
  }

  fatchCourse(){
    this.apiService.courseSelect().subscribe( () => {
      this.course_name = this.apiService.courseSelects.course_name;
      this.course_code = this.apiService.courseSelects.course_code;
      this.course_year = this.apiService.courseSelects.course_year;
    });
  }

  // fatchQuestionCourse(){
  //   this.apiService.getQuestionCourse().subscribe( () => {
  //     this.questions = this.apiService.questionsCourse;
  //   });
  // }

  fatchQuestionCourseSelect(){
    this.apiService.questionCourseSelect().subscribe( () => {
      this.questionsSelect = (this.apiService.questionsCourseSelect).reverse();
    });
  }

  addQuestionCourse(){
    for(this.i=0;this.i<this.delete.length;this.i++){
      this.apiService.questionCourseInsert(this.delete[this.i]).subscribe( () => {
        this.fatchQuestionCourseSelect();
      });
    }
    
  }

  idDelete(id: string){
    this.questionId = id;
    // this.fatchTagsQuestion(id);
    this.delete.push(this.questionId);
    
  }

  filter(event){
    this.filtering = event.target.value;
    if(this.filtering == 'normal'){
      this.fatchQuestionCourseSelect();
    }else if(this.filtering == 'max'){
      this.questionsSelect = (this.apiService.questionsCourseSelect).sort((a,b) => b.question_point - a.question_point);
    }else if(this.filtering == 'min'){
      this.questionsSelect = (this.apiService.questionsCourseSelect).sort((a,b) =>  a.question_point - b.question_point);
    }else{
      this.questionsTag = [];
      (this.apiService.questionsCourseSelect).filter((q) => {
        q.question_tags.filter((tag, index) => {
          if(tag.tag_tag === this.filtering){
            this.questionsTag.push(q);
          }
        })}
      );
      this.questionsSelect = this.questionsTag;
    }

  }

}
