import { Component, OnInit } from '@angular/core';
import { Testcase } from 'app/models/testcase.model';
import { Tag } from 'app/models/tag.model';
import { Question } from 'app/models/question.model';
import { QuestionService } from 'app/services/question.service';
import { ApiService } from 'app/services/api.service';
export interface Tests {
  testcase_testcase: string;
}
@Component({
  selector: 'app-teacher-edit-question',
  templateUrl: './teacher-edit-question.component.html',
  styleUrls: ['./teacher-edit-question.component.scss']
})
export class TeacherEditQuestionComponent implements OnInit {

  question: Question[];
  tags: Tag[];
  testcases: Testcase[];
  name: string;
  proposition: string;
  point: string;
  guide: string;
  tag_tag: string;
  tastcase: string;
  id: string;
  test: string;
  public testImport: string;
  public testBody: string;
  testGuide: string = 'public class Test {';
  testGuide2: string = '}';
  quetionGuide: string = 'public class {}';
  testCase: Tests[] = [];
  error_complie: string;
  example: string;
  toEditQuestion: Question = new Question(this.id, this.name, this.proposition,
     this.point, this.guide, this.test, this.example);

  constructor(
    private apiService: ApiService,
  ) { }

  ngOnInit(): void {
    this.fatchQuestion();
  }

  fatchQuestion(){
    this.apiService.selectQuestion().subscribe( () => {
      this.question = this.apiService.toEditQuestions;
      this.toEditQuestion = JSON.parse(JSON.stringify(this.question));
      this.testGuide = 'public class '+this.toEditQuestion.question_name+'Test {';
    });
    this.apiService.questionTestcase().subscribe( () => {
      this.testImport = this.apiService.testcases1;
      this.testBody = this.apiService.testcases2;
    });
    this.apiService.getTagsQuestion().subscribe( () => {
      this.tags = this.apiService.tags;
    });
  }


  editQuestion(question_name:string, question_point:string,  question_proposition:string,
    question_guide:string,  a:string, b:string, c:string, d:string, question_example: string){
      this.testCase.push({testcase_testcase: a.trim()});
      this.testCase.push({testcase_testcase: b.trim()});
      this.testCase.push({testcase_testcase: c.trim()});
      this.testCase.push({testcase_testcase: d.trim()});
      const toTestcase = (JSON.stringify(this.testCase));
    this.apiService.questionUpdate(question_name, question_proposition, question_point, 
      question_guide, toTestcase, question_example).subscribe( () => {
        this.testCase.splice(0, this.testCase.length);
    });
  }

  onEvent(e: any){
    const latestValue = e.target.value;
    this.testGuide = 'public class '+latestValue+'Test {';
  }
  

  compileQuestion(quetionName: string, quetionGuide: string, a:string, b:string, c:string, d:string){
    // const name = question.question_name;
    this.testCase.push({testcase_testcase: a.trim()});
    this.testCase.push({testcase_testcase: b.trim()});
    this.testCase.push({testcase_testcase: c.trim()});
    this.testCase.push({testcase_testcase: d.trim()});
    const toTestcase = (JSON.stringify(this.testCase));
    this.apiService.questionComplie(quetionName, quetionGuide, toTestcase).subscribe(res =>{
      this.error_complie = this.apiService.error_complies;
      this.testCase.splice(0, this.testCase.length);
    });
  }

}
