import { Component, OnInit } from '@angular/core';
import { QuestionService } from 'app/services/question.service';
import { Question } from 'app/models/question.model';
import { ApiService } from 'app/services/api.service';
import { Testcase } from 'app/models/testcase.model';
import { Tag } from 'app/models/tag.model';
import * as moment from 'moment';
import { matMenuAnimations } from '@angular/material/menu';

@Component({
  selector: 'app-student-work-assingment',
  templateUrl: './student-work-assingment.component.html',
  styleUrls: ['./student-work-assingment.component.scss']
})
export class StudentWorkAssingmentComponent implements OnInit {

  // questions: Array<Question>;
  
  question: Question;
  tags: Tag[];
  testcases: string;
  name: string;
  proposition: string;
  point: string;
  guide: string;
  tag_tag: string;
  tastcase: string;
  id: string;
  startTime: any;
  endTime: any;
  submitsession_status = '';
  test: string;
  public pasteCount: number = 0;
  public pastedText;
  public copyCount: number = 0;
  public copyText;
  submitsession_copy_paste: string;
  example: string;
  toEditQuestion: Question = new Question(this.id, this.name, this.proposition, 
    this.point, this.guide, this.test, this.example);


  constructor(
    private apiService: ApiService,
  ) { }

  ngOnInit(): void {
    this.fatchQuestion();
    this.submitsession_copy_paste = 'No';
    this.startTime = moment().format('HH:mm:ss');
  }

  onPaste(event: ClipboardEvent) {
    this.pasteCount++;
    this.pastedText = event.clipboardData.getData('text');
  }

  onCopy(event: ClipboardEvent) {
    this.copyCount++;
  }

  fatchQuestion(){
    // localStorage.setItem('questionId', questionID);
    this.apiService.studentQuestionSelect().subscribe( (res) => {
      this.question = this.apiService.studentQuestionSelects;
      this.toEditQuestion = JSON.parse(JSON.stringify(this.question));
    });
    this.apiService.questionTestcase().subscribe( (res) => {
      this.testcases = this.apiService.testcases2;
    });
    this.apiService.getTagsQuestion().subscribe( (res) => {
      this.tags = this.apiService.tags;
    });
  }

  save(question_guide: string){
    if(this.pastedText){
      this.submitsession_copy_paste = 'Yes';
    } else {
      this.submitsession_copy_paste = 'No';
    }
    this.submitsession_status = 'Save';
    this.endTime = moment().format('HH:mm:ss');
    const timer = moment.utc(moment(this.endTime,"HH:mm:ss").diff(moment(this.startTime,"HH:mm:ss"))).format("HH:mm:ss");
    this.apiService.studentSave(question_guide, timer, this.submitsession_copy_paste).subscribe( (res) => {});
  }

  public answer_status: string;
  public error_complie: string;
  run(toEditQuestion: Question){
    const question_name = toEditQuestion.question_name;
    const question_guide = toEditQuestion.question_guide;
    const question_point = toEditQuestion.question_point;
    this.apiService.studentRun(question_name, question_guide, question_point).subscribe( (res) => {
      this.answer_status = this.apiService.answer_status;
      this.error_complie = this.apiService.error_complie;
    });
  }

  submit(toEditQuestion: Question){
    if(this.pastedText){
      this.submitsession_copy_paste = 'Yes';
    } else {
      this.submitsession_copy_paste = 'No';
    }
    const question_name = toEditQuestion.question_name;
    const question_guide = toEditQuestion.question_guide;
    const question_point = toEditQuestion.question_point;
    this.endTime = moment().format('HH:mm:ss');
    const timer = moment.utc(moment(this.endTime,"HH:mm:ss").diff(moment(this.startTime,"HH:mm:ss"))).format("HH:mm:ss");
    this.submitsession_status = 'Submit';
    this.apiService.studentSubmit(question_name, question_guide, question_point, timer
      , this.submitsession_copy_paste, this.submitsession_status).subscribe( (res) => {
      
    });
  }

}
