import { Component, OnInit } from '@angular/core';
import { ApiService } from 'app/services/api.service';
import { FormGroup, FormBuilder } from '@angular/forms';
import swal from 'sweetalert2';
import { Router } from '@angular/router';

@Component({
  selector: 'app-course-add-student',
  templateUrl: './course-add-student.component.html',
  styleUrls: ['./course-add-student.component.css']
})
export class CourseAddStudentComponent implements OnInit {

  public course_name: string;
  public course_code: string;
  public course_year: string;
  fileToUpload: File;
  form: FormGroup;
  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.fatchCourse();
    // this.refresh();
    this.form = this.formBuilder.group({
      importfile: ['']
    });
  }

  refresh(): void {
    window.location.reload();
  }

  fatchCourse(){
    this.apiService.courseSelect().subscribe( () => {
      this.course_name = this.apiService.courseSelects.course_name;
      this.course_code = this.apiService.courseSelects.course_code;
      this.course_year = this.apiService.courseSelects.course_year;
    });
  }

  fileChange(event) {
    const fileList: FileList = event.target.files;
    if (fileList.length > 0) {
      this.fileToUpload = fileList[0];
    }
  }

  i: number;
  import() {
    if (!this.fileToUpload) {
      return;
    }
    let formData = new FormData();
    formData.append('file', this.fileToUpload);
    this.apiService.importStudent(formData).subscribe(() => {});
  }


}
