export class Course{
    public course_code: string;
    public course_name: string;
    public course_year: string;

    constructor(
        course_code: string,
        course_name: string,
        course_year: string,
    ){
        this.course_code = course_code;
        this.course_name = course_name;
        this.course_year = course_year;
        
    }

}