import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AuthService } from '../../services/auth/auth.service';
import { Teacher } from 'app/models/teacher.model';
import { TeacherCourseComponent } from 'app/pages/teacher-course';
import { ApiService } from 'app/services/api.service';
import { Student } from 'app/models/student.model';

@Component({
  selector: 'app-common-layout',
  templateUrl: './common-course.component.html',
})
export class CommonCourseComponent implements OnInit {

  user: string ;
  course: string = '';
  students: string = '';
  status: string = '';
  teacher: Teacher;
  toEditTeachers: Teacher;
  toEditStudent: Student;
  picture: string;

  constructor(private authService: AuthService,
              private router: Router,
              private apiService: ApiService
              ) {}

  public ngOnInit() {
    this.check();
    this.fatchProfile();
  }

  fatchProfile(){
    // editProfileTeacherSelect
    if(this.status == 'teacher'){
      this.apiService.editProfileTeacherSelect().subscribe((response: any) => {
      this.toEditTeachers = JSON.parse(JSON.stringify(this.apiService.toEditTeacher));
      this.picture = 'http://localhost/javacodewars/imagesProfile/'+this.toEditTeachers.teacher_picture;
      this.user = this.apiService.usernane;
      });
    }else{
      this.apiService.editProfileStudentSelect().subscribe((response: any) => {
      this.toEditStudent = JSON.parse(JSON.stringify(this.apiService.toEditStudent));
      this.picture = 'http://localhost/javacodewars/imagesProfile/'+this.toEditStudent.student_picture;
      });
    }
  }

  refresh(): void {
    window.location.reload();
  }

  check(){
    this.status = this.apiService.token;
    if(this.status == 'teacher'){
      this.user = this.apiService.userName;
      if(this.apiService.courseId){
        this.course = this.apiService.courseId;
        this.refresh;
      }
    } else if(this.status == 'student'){
      this.students = this.apiService.userName;
      this.refresh;
    }
    
  }

}
