export class Question{
    public question_id: string;
    public question_name: string;
    public question_proposition: string;
    public question_point: any;
    public question_guide: string;
    public question_tags: any;
    public question_example: string;

    constructor( 
        question_id: string,
        question_name: string, 
        question_proposition: string, 
        question_point: any, 
        question_guide: string,
        question_tags: any,
        question_example: string,){
            
            this.question_id = question_id;
            this.question_name = question_name;
            this.question_proposition = question_proposition;
            this.question_point = question_point;
            this.question_guide = question_guide;
            this.question_tags = question_tags;
            this.question_example = question_example;
    }

}