import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AuthService } from '../../services/auth/auth.service';
import { Teacher } from 'app/models/teacher.model';
import { TeacherCourseComponent } from 'app/pages/teacher-course';
import { ApiService } from 'app/services/api.service';
import { Student } from 'app/models/student.model';

@Component({
  selector: 'app-common-layout',
  templateUrl: './common-layout.component.html',
})
export class CommonLayoutComponent implements OnInit {

  user: string ;
  course: string = '';
  students: string = '';
  status: string = '';
  teacher: Teacher;
  toEditTeachers: Teacher;
  toEditStudent: Student;
  picture: string;

  constructor(private authService: AuthService,
              private router: Router,
              private apiService: ApiService
              ) {}

  public ngOnInit() {
    // this.check();
    this.fatchProfile();
  }

  fatchProfile(){
    // editProfileTeacherSelect
    this.apiService.editProfileTeacherSelect().subscribe((response: any) => {
      this.toEditTeachers = JSON.parse(JSON.stringify(this.apiService.toEditTeacher));
      this.picture = 'http://localhost/javacodewars/imagesProfile/'+this.toEditTeachers.teacher_picture;
      this.user = this.apiService.usernane;
    });
  }

  refresh(): void {
    window.location.reload();
  }


}
