import { Component, OnInit } from '@angular/core';
import { QuestionService } from 'app/services/question.service';
import { Question } from 'app/models/question.model';
import { Router } from '@angular/router';
import { ApiService } from 'app/services/api.service';
import { Course } from 'app/models/course.model';
import { QuestionTags } from 'app/models/question-tag.model';
import { Tag } from 'app/models/tag.model';

@Component({
  selector: 'app-student-excersie',
  templateUrl: './student-excersie.component.html',
  styleUrls: ['./student-excersie.component.css']
})
export class StudentExcersieComponent implements OnInit {


  public course_name: string;
  public course_code: string;
  public course_year: string;
  questions: Array<QuestionTags> = [];
  questionsTag: Array<QuestionTags> = [];
  courseName: Course;
  tags: Array<Tag> = [];
  filtering: any;


  constructor(
    private questionService: QuestionService,
    private router: Router,
    private apiService: ApiService
  ) { }



  ngOnInit(): void {
    this.fatchQuestionCourse();
    this.fatchCourse();
    this.fatchTags();
    this.filtering = 'normal';
  }

  fatchCourse(){
    this.apiService.courseSelect().subscribe( () => {
      this.course_name = this.apiService.courseSelects.course_name;
      this.course_code = this.apiService.courseSelects.course_code;
      this.course_year = this.apiService.courseSelects.course_year;
    });
  }

  fatchTags(){
    this.apiService.allTags().subscribe( () => {
      this.tags = this.apiService.tags_tags;
    });
  }

  fatchQuestionCourse(){
    this.apiService.studentQuestionCourse().subscribe( () => {
      this.questions = (this.apiService.questionstudentCourse).reverse();
    });
  }

  gotoQuestion: Question;
  goToAssingment(questionId: string){
    localStorage.setItem('questionId', questionId);
    this.apiService.studentQuestionSelect().subscribe( () => {
      this.gotoQuestion = this.apiService.studentQuestionSelects;
      this.router.navigate(['/student/student-work']);
    });
  }


  filter(event){
    this.filtering = event.target.value;
    if(this.filtering == 'normal'){
      this.fatchQuestionCourse();
    }else if(this.filtering == 'max'){
      this.questions = (this.apiService.questionstudentCourse).sort((a,b) => b.question_point - a.question_point);
    }else if(this.filtering == 'min'){
      this.questions = (this.apiService.questionstudentCourse).sort((a,b) =>a.question_point - b.question_point);
    }else{
      this.questionsTag = [];
      (this.apiService.questionstudentCourse).filter((q) => {
        q.question_tags.filter((tag, index) => {
          if(tag.tag_tag === this.filtering){
            this.questionsTag.push(q);
          }
        })}
      );
      this.questions = this.questionsTag;
    }

  }
}
