    public addQuestion (question_name: string, question_proposition: string, question_point: string,
        question_guide: string, testcase_testcase: string, tag_tag: Tags[], question_example: string):Observable<boolean>{
        this.fetchLocalStorage();
        const toTestcase = JSON.parse(JSON.stringify(testcase_testcase));
        const toTag = JSON.parse(JSON.stringify(tag_tag));
        let url = this.global.baseurl+"question.php";
        const params = new HttpParams().set('teacher_user', this.userName).set('question_name', question_name).set('question_point', question_point)
        .set('question_proposition', question_proposition).set('question_guide', encodeURIComponent(question_guide))
        .set('testcase_testcase', toTestcase).set('tag_tag', toTag).set('question_example', encodeURIComponent(question_example));
            return this.httpClient.post( url,params.toString(),{ headers: new HttpHeaders().set('Content-Type', 
                'application/x-www-form-urlencoded' )}).pipe( map((res: any) => {
                if (res.message == "Success"){
                    swal.fire(
                        '',
                        ' ' +question_name+' ',
                        'success'
                    ).then(() => {
                        this.router.navigate(['/teacher/teacher-assingment']);
                    });
                    return true;
                }else if (res.message == "Unsuccess question name already exists "){
                  swal.fire(
                  '',
                  '' +question_name+ ' ',
                  'warning'
                  ).then(() => {
                    // this.router.navigate(['/app/teacher-assingment']);
                  });
                  return true;
                }else{
                    return false;
                }
            }));
    }

