    public error_complies: string = '';
    public questionComplie ( question_name: string, question_guide: string,
        testcase_testcase: string):Observable<boolean>{
        this.fetchLocalStorage();
        let guide = encodeURIComponent(question_guide);
        const toTestcase = JSON.parse(JSON.stringify(testcase_testcase));
        let url = this.global.baseurl+"questionComplie.php";
        const params = new HttpParams().set('question_name', question_name).set('question_guide', guide)
        .set('testcase_testcase', (toTestcase)).set('teacher_user', this.userName);

          return this.httpClient.post( url,params.toString(),{ headers: new HttpHeaders().set('Content-Type', 
          'application/x-www-form-urlencoded' )}).pipe( map((res: any) => {
          if (res.message == "Success"){
            this.error_complies = res.message;
            swal.fire(
              'Compile Success',
              'Compile ' +question_name+ ' Success',
              'success'
              ).then(() => {});
              return true;
          }else if (res.message == "Unsuccess"){
              this.error_complies = res.data[0].error_complie;
              return true;
          }else if(res.message == "Unsuccess Unsuccess"){
               return false;
          }else {
          }
      }));
    }

