    importStudent(file): Observable<boolean> {
        this.fetchLocalStorage();
        let url = this.global.baseurl+"studentInsert.php";
        file.append('course_id', this.courseId);
        const options = {
            // headers: new HttpHeaders().set('Content-Type','application/x-www-form-urlencoded'),
            params: new HttpParams().set('course_id', this.courseId),
        }
        
            return this.httpClient.post(url,file,options).pipe( map((res: any) => {
                if (res.message == "Success"){
                  if(res.data == ""){
                    // res.data = '-';
                    swal.fire(
                      '',
                      '',
                      'success'
                    ).then(() => {
                            // this.router.navigate(['/']);
                    });
                    return true;
                  } else {
                    swal.fire(
                      '',
                      ' \n  \n ' +res.data+'  ',
                      'success'
                    ).then(() => {
                            // this.router.navigate(['/']);
                    });
                    return true;
                  }
                }else if (res.message == "Unsuccess, Student ID is duplicated with in the file."){
                  swal.fire(
                      'Unsuccess',
                      'Unsuccess, Student ID is duplicated with in the file\n'+res.data,
                      'error'
                  ).then(() => {
                          // this.router.navigate(['/']);
                  });
                  return true;
              }else if (res.message == "Unsuccess, Student email is duplicated with in the file."){
                swal.fire(
                    'Unsuccess',
                    'Unsuccess, Student email is duplicated with in the file.\n'+res.data,
                    'error'
                ).then(() => {
                        // this.router.navigate(['/']);
                });
                return true;
              }else if (res.message == "Unsuccess, Student is duplicated with in the Class."){
                swal.fire(
                    'Unsuccess',
                    'Unsuccess, Student is duplicated with in the Class.\n'+res.data,
                    'error'
                ).then(() => {
                        // this.router.navigate(['/']);
                });
                return true;
              }else{
                return false;
            }
        }));
    }
    
