    public tags: Array<Tag>;
    public getTagsQuestion ():Observable<boolean>{
        let url = this.global.baseurl+"questionTag.php";
        this.fetchLocalStorage();
        const params = new HttpParams().set('question_id', this.questionId);
          return this.httpClient.post( url,params.toString(),{ headers: new HttpHeaders().set('Content-Type', 
            'application/x-www-form-urlencoded' )}).pipe( map((res: any) => {
            if (res.message == "Success"){
              this.tags = res.data;
              return true;
            }else{
              return false;
            }
          }));
      }

