      public editProfileEmail (email: string):Observable<boolean>{
        let url = this.global.baseurl+"editProfileEmail.php";
        this.fetchLocalStorage();
        const params = new HttpParams().set('user', this.userName).set('email', email);
          return this.httpClient.post( url,params.toString(),{ headers: new HttpHeaders().set('Content-Type', 
            'application/x-www-form-urlencoded' )}).pipe( map((res: any) => {
            if (res.message == "Success , Change teacher email"){
              swal.fire(
                'Save Complete',
                ' Email : ' + email,
                'success'
              ).then(() => {
                  // this.router.navigate(['/']);
              });
              return true;
            }else if (res.message == "Success, Change student email"){
              swal.fire(
                'Save Complete',
                ' Email : ' + email,
                'success'
              ).then(() => {
                  // this.router.navigate(['/']);
              });
              return true;
            }else if (res.message == "Unsuccess. Email already exists"){
              swal.fire(
                'Unsuccess',
                'Unsuccess. Email already exists Email : ' + email,
                'error'
              ).then(() => {
                  // this.router.navigate(['/']);
              });
              return true;
            }else{
              return false;
            }
          }));
      }

