import { Component, OnInit } from '@angular/core';
import { Router }  from '@angular/router';
import { ApiService } from 'app/services/api.service';
import { Course } from 'app/models/course.model';
import { Question } from 'app/models/question.model';
import { StudentPoint } from 'app/models/student-point.model';

@Component({
  selector: 'app-student-home',
  templateUrl: './student-home.component.html',
  styleUrls: ['./student-home.component.css']
})
export class StudentHomeComponent implements OnInit {

  courses: Array<Course> = [];
  courseName: Course;
  public course_name: string;
  public course_code: string;
  public course_year: string;
  questions: Array<Question> = [];
  questionsTop: Array<Question> = [];
  questionsTop1: Question;
  questionsTop2: Question;
  questionsTop3: Question;
  point: number;
  i: number;
  students: Array<StudentPoint> = [];
  studentsTop: Array<StudentPoint> = [];
  studentsTop3: Array<StudentPoint> = [];
  courseStudentId;

  
  constructor(
    private apiService: ApiService,
    private router: Router,
  ) { }

  ngOnInit(): void {
    this.fatchCourse();
    this.fatchCourseName();
    this.fatchPoint();
    this.fatchquestionCourse();
    this.fatchStudentRank();
  }

  fatchCourseName(){
    this.apiService.courseSelect().subscribe( () => {
      this.course_name = this.apiService.courseSelects.course_name;
      this.course_code = this.apiService.courseSelects.course_code;
      this.course_year = this.apiService.courseSelects.course_year;
    });
  }

  fatchCourse(){
    this.apiService.studentCourseAll().subscribe( () => {
      this.courses = this.apiService.studentCourses;
    });
    
    this.apiService.courseSelect().subscribe( () => {
      this.courseName = this.apiService.courseSelects;
    });
  }

  fatchPoint(){
    this.apiService.studentPoint().subscribe( () => {
      this.point = this.apiService.points;
    });
  }

  fatchStudentRank(){
    this.apiService.studentRank().subscribe( (res) => {
      this.students = this.apiService.studentRanks;
      this.studentsTop = this.students.sort((a,b) => b.submitsession_point_total - a.submitsession_point_total);
      this.studentsTop3 = [this.studentsTop[0], this.studentsTop[1], this.studentsTop[2]]
    });
    
  }

  fatchquestionCourse(){
    this.questionsTop.splice(0, this.questionsTop.length);
    this.apiService.studentQuestionCourse().subscribe( () => {
      this.questions = (this.apiService.questionstudentCourse).reverse();
      this.questionsTop = [this.questions[0], this.questions[1], this.questions[2]];
    });
  }

  gotoQuestion: Question;
  goToAssingment(questionId: string){
    localStorage.setItem('questionId', questionId);
    this.apiService.studentQuestionSelect().subscribe( () => {
      this.gotoQuestion = this.apiService.studentQuestionSelects;
      this.router.navigate(['/student/student-work']);
    });
  }

  selectCourse(courseId: string){
    localStorage.setItem('courseId', courseId);
      this.apiService.courseSelect().subscribe( () => {
        this.fatchCourseName();
        this.fatchPoint();
        this.fatchquestionCourse();
        this.fatchStudentRank();
    });
  }

}
