import { Injectable } from '@angular/core';
import { Course } from 'app/models/course.model';
import { HttpClient, HttpParams, HttpHandler, HttpHeaders } from '@angular/common/http';
import { GlobalService } from './global/global.service';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import swal from 'sweetalert2';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class CourseService {

  public courses: Course[];

  constructor(
    private httpClient: HttpClient,
    private global: GlobalService,
    private router: Router
  ) { }

  public courseHide (course_id : string, course_status: string):Observable<boolean>{
    let url = this.global.baseurl+"courseHide.php";
    // teacher_user = 'baewjaiboss';
    const params = new HttpParams().set('course_id ', course_id ).set('course_status ', course_status);
      return this.httpClient.post( url,params.toString(),{ headers: new HttpHeaders().set('Content-Type', 
        'application/x-www-form-urlencoded' )}).pipe( map((res: any) => {
        if (res.message == "Success"){
          return true;
        }else{
          return false;
        }
      }));
  }

  public courseInsertQuestion (course_id : string, question_id: string):Observable<boolean>{
    let url = this.global.baseurl+"questionCourseInsert.php";
    // teacher_user = 'baewjaiboss';
    const params = new HttpParams().set('course_id ', course_id ).set('question_id ', question_id);
      return this.httpClient.post( url,params.toString(),{ headers: new HttpHeaders().set('Content-Type', 
        'application/x-www-form-urlencoded' )}).pipe( map((res: any) => {
        if (res.message == "Success"){
          return true;
        }else{
          return false;
        }
      }));
  }

  public courseSelectQuestion (course_id : string):Observable<boolean>{
    let url = this.global.baseurl+"questionCourseSelect.php";
    // teacher_user = 'baewjaiboss';
    const params = new HttpParams().set('course_id ', course_id );
      return this.httpClient.post( url,params.toString(),{ headers: new HttpHeaders().set('Content-Type', 
        'application/x-www-form-urlencoded' )}).pipe( map((res: any) => {
        if (res.message == "Success"){
          return true;
        }else{
          return false;
        }
      }));
  }

}
