import { Component, OnInit } from '@angular/core';
import { CourseAddStudentComponent } from '../course-add-student';
import { ApiService } from 'app/services/api.service';
import { Student } from 'app/models/student.model';
import swal from 'sweetalert2';

@Component({
  selector: 'app-course-list-student',
  templateUrl: './course-list-student.component.html',
  styleUrls: ['./course-list-student.component.css']
})
export class CourseListStudentComponent implements OnInit {

  public course_name: string;
  public course_code: string;
  public course_year: string;
  students: Array<Student>;
  i: number;
  delete = [];
  constructor(private apiService: ApiService) { }

  ngOnInit(): void {
    this.fatchCourse();
    this.fatchStudentsCourse();
  }

  fatchCourse(){
    this.apiService.courseSelect().subscribe( () => {
      this.course_name = this.apiService.courseSelects.course_name;
      this.course_code = this.apiService.courseSelects.course_code;
      this.course_year = this.apiService.courseSelects.course_year;
    });
  }

  fatchStudentsCourse(){
    this.apiService.studentCourse().subscribe( () => {
      this.students = this.apiService.studentsCourse;
    });
  }

  public studentID: string;
  idDelete(id: string){
    this.studentID = id;
    this.delete.push(this.studentID);
    
  }

  deleteStudent(){
    swal.fire({
      title: '',
      text: '?' ,
      showCancelButton: true,
      confirmButtonColor: '#dc3545',
      cancelButtonColor: 'btn btn-danger',
      confirmButtonText: '',
      reverseButtons: true,
    }).then((result) => {
      if (result.value) {
        for(this.i=0;this.i<this.delete.length;this.i++){
          this.apiService.studentDelete(this.delete[this.i]).subscribe( (res) => {
            this.fatchStudentsCourse();
          });
        }
        swal.fire(
          '!',
          '',
          'success'
        )
      } else if (
        result.dismiss === swal.DismissReason.cancel
      ) {
        swal.fire(
          '',
          '',
          'error'
        )}
    });
  }

}
