import { Injectable, ErrorHandler } from '@angular/core';
import { Question } from 'app/models/question.model';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { GlobalService } from './global/global.service';
import { Observable, throwError  } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { Testcase } from 'app/models/testcase.model';
import { Tag } from 'app/models/tag.model';
import swal from 'sweetalert2';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root',
})


export class QuestionService {

  questions: Question[];
  testCases: Testcase[];
  public tags: Tag[];
  public toEditQuestions: Question[];
  public questionsCourse: Question[];



  constructor(
    private httpClient: HttpClient,
    private global: GlobalService,
    private router: Router
  ) { }

  public question: Array <Question>;
  public getQuestion(): Observable<Question[]>{
    let url = this.global.baseurl +"questionStorage.php";
    let body = "";
    let callUrl: string = url + "?" + body;
    return this.httpClient.get(callUrl).pipe(map((res:any) => {
      this.question = res.data;
      if (res.message != "Success"){
        return [];
      }

    let arr:Question[] = [];
      return arr;
    })
    );
  }

  public getTagsQuestion (question_id: string):Observable<boolean>{
    let url = this.global.baseurl+"questionTag.php";
    const params = new HttpParams().set('question_id', question_id);
      return this.httpClient.post( url,params.toString(),{ headers: new HttpHeaders().set('Content-Type', 
        'application/x-www-form-urlencoded' )}).pipe( map((res: any) => {
        if (res.message == "Success"){
          this.tags = res.data;
          return true;
        }else{
          return false;
        }
      }));
  }


    public deleteQuestion ( question_id: string):Observable<boolean>{
        let url = this.global.baseurl+"questionDelete.php";
        const params = new HttpParams().set('question_id', question_id);
          return this.httpClient.post( url,params.toString(),{ headers: new HttpHeaders().set('Content-Type', 
            'application/x-www-form-urlencoded' )}).pipe( map((res: any) => {
            if (res.message == "Success"){
              return true;
            }else{
              return false;
            }
          }));
    }

      public selectQuestion ( question_id: string):Observable<boolean>{
        let url = this.global.baseurl+"questionSelect.php";
        const params = new HttpParams().set('question_id', question_id);
          return this.httpClient.post( url,params.toString(),{ headers: new HttpHeaders().set('Content-Type', 
            'application/x-www-form-urlencoded' )}).pipe( map((res: any) => {
            if (res.message == "Success"){
              this.toEditQuestions = res.data[0];
              this.router.navigate(['/teacher/teacher-edit-question']);
              return true;
            }else{
              return false;
            }
          }));
      }
  
      public questionTestcase (question_id: string):Observable<boolean>{
        let url = this.global.baseurl+"questionTestcase.php";
        const params = new HttpParams().set('question_id', question_id);
          return this.httpClient.post( url,params.toString(),{ headers: new HttpHeaders().set('Content-Type', 
            'application/x-www-form-urlencoded' )}).pipe( map((res: any) => {
            if (res.message == "Success"){
              return true;
            }else{
              return false;
            }
          }));
      }
}
